body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.correct-animation {
  -webkit-animation-name: correct;
          animation-name: correct;
}

.wrong-animation {
  -webkit-animation-name: wrong;
          animation-name: wrong;
}

.correct-animation {
  -webkit-animation: correct 1s forwards;
          animation: correct 1s forwards;
}

.wrong-animation {
  -webkit-animation: wrong 5s forwards;
          animation: wrong 5s forwards;
}

.correct-animation,
.wrong-animation {
  display: inline-block;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  background-color: transparent;
  color: inherit;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  transition: color 1s, text-shadow 1s;
}

.correct-animation {
  background-color: #18b3b5;
}

.wrong-animation {
  background-color: #a73923;
}

.correct-animation {
  color: #18b3b5;
}

.wrong-animation {
  color: #a73923;
}

@-webkit-keyframes correct {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@keyframes correct {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 0;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
  }
}

@-webkit-keyframes wrong {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0;
  }
}

@keyframes wrong {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0;
  }
}


