.correct-animation {
  animation-name: correct;
}

.wrong-animation {
  animation-name: wrong;
}

.correct-animation {
  animation: correct 1s forwards;
}

.wrong-animation {
  animation: wrong 5s forwards;
}

.correct-animation,
.wrong-animation {
  display: inline-block;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
  background-color: transparent;
  color: inherit;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  transition: color 1s, text-shadow 1s;
}

.correct-animation {
  background-color: #18b3b5;
}

.wrong-animation {
  background-color: #a73923;
}

.correct-animation {
  color: #18b3b5;
}

.wrong-animation {
  color: #a73923;
}

@keyframes correct {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes wrong {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}

